import React, { useState, useEffect } from 'react';
import openSocket from 'socket.io-client';
import './App.css';

function App() {

  const [socket, setSocket] = useState(openSocket('https://morning-journey-81748.herokuapp.com/'));
  const [laskuri, setLaskuri] = useState(0);
  const [voitto, setVoitto] = useState('');
  const [voittoLaskuri, setVoittoLaskuri] = useState('');
  const [pisteet, setPisteet] = useState('');
  const [tilanne, setTilanne] = useState(true);

  useEffect(() => {
    tarkastaVoitto(laskuri);
    tarkastaPisteet(pisteet);
  }, [pisteet]);

  const tarkastaVoitto = (laskuri) => {
    if(laskuri > 0) {
      if(laskuri % 500 === 0) {
        setVoitto("Voitit 250 pistettä!")
      } else if (laskuri % 100 === 0) {
        setVoitto("Voitit 40 pistettä!")
      } else if (laskuri % 10 === 0) {
        setVoitto("Voitit 5 pistettä!")
    } else {
      return;
    }};
  };

  const tarkastaPisteet = (pisteet) => {
    if(pisteet === 0 ) {
      setTilanne(!tilanne)
    };
  };

  const pelaa = () => {
    socket.emit('pelaa');
    setVoitto('')
  };

  const uusiPeli = () => {
    socket.emit('uusiPeli');
    setTilanne(!tilanne);
  }

  socket.on('counter', function (data) {
    setLaskuri(data.laskuri);
    setVoittoLaskuri(data.voittolaskuri);
    setPisteet(data.pisteet);
  });

  return (
    tilanne 
    ?
    <div className="App">
      <button onClick={pelaa} style={{fontSize: 100, marginTop: 50}}>Pelaa</button>
      <br/>
      <label>Painalluksia seuraavaan voittoon: {voittoLaskuri}
      <br/>
       Pisteet: {pisteet}</label>
      <br/>
      <label>Voitto: {voitto}</label>
    </div>
    :
    <div className="App">
      <h1>Pisteet loppu!</h1>
      <button onClick={uusiPeli}>Uusi peli</button>
    </div>
  );
};

export default App;
